.header {
  width: 100%;
  position: relative;
}

.header__head {
  display: flex;
  flex-direction: column;
}

.head__links {
  display: flex;
  gap: 20px;
  padding: 12px 0;
}

.head__link {
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  transition: 0.25s;
  color: #000;

  &:hover {
    color: var(--second-color);
  }
}

.header__head-border-bottom {
  width: 200%;
  transform: translateX(-25%);
  border-bottom: 1px solid rgb(243, 243, 247);
}

.header__container {
  display: flex;
  flex-direction: column;
  padding-bottom: 25px;
  gap: 16px;
}

.header__main-block {
  display: flex;
  gap: 40px;
  align-items: center;
}

.header__logo {
  width: 250px;
}

.header__delivery-text {
  font-size: 18px;
  line-height: 1.2;
  font-weight: 500;

  .select-sity {
    color: var(--second-color);
    transition: 0.25s;

    &:hover {
      color: var(--second-color-hover);
    }
  }
}

.header__phone-number {
  font-size: 18px;
  line-height: 1.2;
  font-weight: 500;
  color: #000;
  transition: 0.25s;

  &:hover {
    color: var(--second-color);
  }
}

.mobile__header {
  display: none;
  z-index: 25;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  transition: background-color 0.25s;
  padding: 12px 0;

  &.header--active {
    background-color: #000;
    padding: 12px var(--container-offset);
    margin: 0 calc(var(--container-offset) * -1);
  }
}

.header__logo-mobile {
  width: 200px;
}

.mobile__phone-number,
.mobile__delivery {
  display: none;
}

.mobile__menu {
  display: contents;
  transform: translateY(-200%);
  transition: 0.25s;

  &.menu--active {
    z-index: 20;
    transform: translateY(0);
    width: 100%;
    height: 100vh;
    padding-left: 35px;
    top: var(--header-height);
    flex-direction: column;
    background-color: silver;
    margin: 0 calc(var(--container-offset) * -1);
    padding-bottom: 100px;

    .header__logo,
    .header__phone-number,
    .header__delivery-text,
    .header__phone-number {
      display: none;
    }

    .mobile__delivery{
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;

      .delivery-text {
        display: flex;
        flex-direction: column;
        gap: 3px;


        .delivery-adress {
          font-size: 20px;
          letter-spacing: 0.4px;
          font-weight: 400;
          color: #fff;
        }

        .delivery__subtext {
          font-size: 13px;
          line-height: 1;
          font-weight: 400;
          color: #fff;
          opacity: 0.5;
        }
      }

      .delivery-img {
        width: 24px;
        display: flex;
        align-items: center;
        position: absolute;
        left: -30px;
      }

    }

    .mobile__phone-number {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;

      .phone-text {
        display: flex;
        flex-direction: column;
        gap: 3px;


        .phone-number {
          font-size: 20px;
          letter-spacing: 0.4px;
          font-weight: 400;
          color: #fff;
        }

        .phone__subtext {
          font-size: 13px;
          line-height: 1;
          font-weight: 400;
          color: #fff;
          opacity: 0.5;
        }
      }

      .phone-img {
        width: 24px;
        display: flex;
        align-items: center;
        position: absolute;
        left: -30px;
      }
    }
  }
}

.burger {
  --burger-width: 24px;
  --burger-height: 24px;
  --burger-line-height: 2px;

  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;
  background-color: transparent;
  margin-left: auto;
  cursor: pointer;

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: var(--burger-line-height);
    background-color: currentColor;
    transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
  }

  &::before {
    top: 0;
  }

  &::after {
    top: calc(100% - var(--burger-line-height));
  }

  &__line {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    height: var(--burger-line-height);
    background-color: currentColor;
    transform: translateY(-50%);
    transition: transform 0.3s ease-in-out;
  }

  &--active {
    color: #fff;

    &::before {
      top: 50%;
      transform: rotate(45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    &::after {
      top: 50%;
      transform: rotate(-45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    .burger__line {
      transform: scale(0);
      transition: transform 0.3s ease-in-out;
    }
  }
}


.header__order {
  color: #fff;
  padding: 8px 20px;
  font-size: 16px;
  line-height: 24px;
  background-color: var(--second-color);
  transition: 0.25s;
  border-radius: 100px;
  margin-left: auto;

  &:hover {
    background-color: var(--second-color-hover);
  }
}

.nav-logo {
  display: none;
}

.nav-menu-line {
  display: flex;
  gap: 20px;
  align-items: center;
}

.nav-list {
  list-style: none;
  display: flex;
  gap: 20px;
}

.nav-list__link {
  font-weight: 600;
  color: #000;
  font-size: 14px;
  transition: 0.25s;

  &:hover {
    color: var(--second-color);
  }
}

.storyboard {
  width: 100%;
  position: relative;
}

.swiper__storyboard {
  width: 100%;
  height: 100%;
}

.storyboard__slide {
  min-width: 200px;
  width: auto;
  border-radius: 20px;
  overflow: hidden;
  cursor: pointer;
}

.storyboard-bg {
  width: 100%;
  aspect-ratio: 4/5;
  position: relative;

  img {
    position: absolute;
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center center;
  }
}

.storyboard-btn {
  position: absolute;
  top: 50%;
  height: 48px;
  width: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  svg {
    height: 24px;
    width: 14px;
    object-fit: contain;

    use {
      fill: none;
      stroke: var(--second-color);
      stroke-linecap: round;
      stroke-width: 2px;
      stroke-linejoin: round;
      transition: 0.25s;
    }
  }

  &:hover {
    svg {
      use {
        stroke: var(--second-color-hover);
      }
    }
  }

  &.prev {
    transform: translate(-100%, -50%);
    left: 0;

    svg {
      transform: rotate(180deg);
    }
  }

  &.next {
    transform: translate(100%, -50%);
    right: 0;
  }

  &.swiper-button-disabled {
    display: none;
  }
}



.often-order {
  width: 100%;
  height: 100%;

  .swiper-wrapper {
    // Это для box-shadow на додо такие же цифры
    padding-top: 10px;
    padding-bottom: 20px;
  }
}

.often-order__slide {
  display: flex;
  gap: 10px;
  align-items: center;
  padding: 16px;
  min-width: 260px;
  box-shadow: rgba(6, 5, 50, 0.15) 0px 4px 22px -6px;
}

.often-order__slide-img {
  width: calc(1/3 * 100%);
  height: auto;
}

.often-order__slide-text {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.often-order__name {
  font-weight: 600;
  font-size: 18px;
  line-height: 20px;
}

.often-order__cost {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  position: relative;

  &.old {
    span {
      position: relative;
      display: inline-block;
    }

    span::before {
      content: '';
      position: absolute;
      width: 100%;
      height: 2px;
      background-color: red;
      top: 75%;
      left: 0;
      transform: rotate(-15deg);
      transform-origin: 0 100%;
    }

  }
}


@media (max-width:1370px) {
  .storyboard-btn {
    display: none;
  }
}

@media (max-width:978px) {
  .mobile__header {
    display: flex;
  }

  .mobile__menu{
    position: absolute;
    display: flex;
  }
}
